<template>
  <div class="container">
    <div class="row">
      <div v-for="social in socials" class="box">
        <div class="title">{{ social.title }}</div>
        <div class="text">
          <div v-for="text in social.texts">{{ text }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'footer',
  data() {
    return {
      socials: [
        {
          title: '社会认可',
          texts: ['社团整体3个', '阿克苏诺贝尔奖', '北京市志愿服务大赛金奖'],
        },
        {
          title: '媒体报道',
          texts: ['协会是你爸爸', '协会是你爸爸', '协会是你爸爸'],
        },
        {
          title: '合作单位',
          texts: ['协会的爸爸', '协会的爸爸', '协会的爸爸'],
        },
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
.container {
  overflow: hidden;
  width: 100%;
  background: url("../images/2.jpg") no-repeat center center;
  color: white;
  .row {
    margin: 20px 0;
    display: flex;
    justify-content: space-around;
    .title {
      font-size: 25px;
      font-weight: bold;
      padding: 5px 0;
    }
    .text {
      font-size: 14px;
      div {
        padding: 5px 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .container {
    .row {
      padding: 0 50px;
      flex-direction: column;
      .title {
        font-size: 20px;
        padding: 5px 0;
      }
      .text {
        div {
          padding: 3px 0;
        }
      }
    }
  }
}
</style>



